<template>
    <div id="PagoProductos">
        <h3>Pagos</h3><br>
        <div v-for="(info, index) in compraBebidas" v-bind:key="index" class="contenedorPagos">
            <p> {{info.nombreBebidaEnviada }}</p>
            <strong><p>{{ info.costoBebidaEnviada }}</p></strong>
            <button v-on:click.prevent="eliminarBebida(index)">Eliminar</button>
            
        </div>
        <button v-on:click.prevent="calcularCosto" id="btnPagar">Calcular total</button>
        <div v-if="mostrar==true">
            <p>El Subtotal es = {{ acumulado }}</p>
            <p>El iva es = {{ iva}}</p>
            <p id="totalPago">Su total es: {{ valorFinalaPagar }}</p>
        </div>
    </div>
</template>
<script>
export default{
    name:'PagoProductos',
    props:{
        compraBebidas:{
            type: Array,
            required: true,
        }
    },   
    data: function(){
        return{
            acumulado:0,
            iva:0,
            valorFinalaPagar:0,
            mostrar: false,
        }
    },
    methods:{

        calcularCosto: function(){
            this.mostrar= true;
            for(let datos of this.compraBebidas){
                this.acumulado =  this.acumulado + datos.costoBebidaEnviada;
            }
            this.calcularIva();
            this.mostrar= true;
            let elBotonPagar = document.getElementById('btnPagar');
            elBotonPagar.disabled= true;
        },
        calcularIva: function(){
            this.iva= this.acumulado*19/100;
            this.valorFinalaPagar = this.acumulado + this.iva;
        },
        eliminarBebida: function(indiceEliminar){
            this.mostrar= false;
            let elBotonPagar = document.getElementById('btnPagar');
            elBotonPagar.disabled= false;
            this.acumulado= 0;
            this.$emit('elementoAEliminar', indiceEliminar);

        }
    }
}

</script>    
<style scoped>

.contenedorPagos{
    display: flex;
    justify-content: space-between;
}
#totalPago{
    color:white;
    font-size: 25px;
}
button{
    border-radius: 10px;
    background-color: blue;
    color: white;
}

</style>