<template>

    <div id="ComponentePadre">
        <h1>Proyecto Productos con VueJS</h1>
        <div class="container">
            <div class="row">
                <div class="col-md-9 bg-info">
                    <ProductosTragos
                    v-bind:informacionBebidas= datosCards
                    v-on:bebidaSeleccionada="procesarBebidaSeleccionada">

                    </ProductosTragos>
                </div>
                <div class="col-md-3 bg-primary">
                    <PagosProductos
                    v-bind:compraBebidas="arregloProductoSeleccionado"
                    v-on:elementoAEliminar="procesarEliminarBebida">
                    
                    </PagosProductos>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import ProductosTragos from '@/components/ProductosTragos.vue';
import PagosProductos from '@/components/PagosProductos.vue';
export default{
    name: "ComponentePadre",
    components: { 
        ProductosTragos,
        PagosProductos
    },
    data: function(){
        return{
            //datos cards es un arreglo de ese objeto
            datosCards:[
            {   
                imgBebida:'https://comidaschilenas.com/wp-content/uploads/2022/12/Receta-de-cola-de-mono-Comidas-Chilenas.jpg',
                tituloBebida: 'Cola de Mono',
                descripcionBebida:'El colemono​ o cola de mono es un cóctel de la gastronomía de Chile, hecho con aguardiente, leche, café, azúcar y especias.',
                costoBebida:20000
            },
            {   
                imgBebida:'https://www.cocina-chilena.com/base/stock/Recipe/355-image/355-image_web.jpg',
                tituloBebida: 'Terremoto',
                descripcionBebida:'Es un coctel típico de la zona del centro de chile. Generalmente consumido en las fechas patrias.',
                costoBebida:20000
            },
            {   
                imgBebida:'https://www.recetasnestlecam.com/sites/default/files/styles/recipe_detail_desktop/public/srh_recipes/5338c89f4cf8fbd2a8211fb423c88751.webp?itok=ty-AWxmH',
                tituloBebida: 'Chocorron',
                descripcionBebida:'Sus ingredientes son: Ron (ojalá del más barato y rancio de todos), hielo a gusto y un toque de leche con chocolate',
                costoBebida:10000
            },
            {   
                imgBebida:'https://www.latercera.com/resizer/O4MlbouBcsYU4DPCR3tVq4QIeV4=/768x0/smart/filters:quality(70):format(webp):no_upscale()/arc-anglerfish-arc2-prod-copesa.s3.amazonaws.com/public/5QGNGTOBQJFTBPZKIWSKAZRWQY.jpg',
                tituloBebida: 'Jote Chileno',
                descripcionBebida:'Un trago chileno, Este combinado no es nada más que una mezcla de vino tinto con coca cola',
                costoBebida:5000
            },
            {   
                imgBebida:'https://recetasdesopaipillas.com/wp-content/uploads/2020/11/Receta-de-malta-con-huevo.jpg?ezimgfmt=ngcb1/notWebP',
                tituloBebida: 'Malta con Huevo',
                descripcionBebida:'En un vaso de licuadora agregar la malta, los huevos enteros y 4 cucharadas de azúcar.',
                costoBebida:20000
            },
            {   
                imgBebida:'https://elsoldeiquique.cl/wp-content/uploads/2018/11/pichi-canguro.png',
                tituloBebida: 'Pichi de Canguro',
                descripcionBebida:'La mezcla es fácil, vierte en un vaso de vidrio, ya que de plástico le da un sabor medio amargoso',
                costoBebida:20000
            },
            
        ],
        arregloProductoSeleccionado:[
            

        ],
            
        }
    },
    methods:{
        procesarBebidaSeleccionada: function(indiceBebida){;
            console.log('Indice Padre: ', indiceBebida);
            let objetoIdentificadoBebida = this.datosCards[indiceBebida];
            let objetoEnviar={
                nombreBebidaEnviada: objetoIdentificadoBebida.tituloBebida,
                costoBebidaEnviada : objetoIdentificadoBebida.costoBebida,
            }
            this.arregloProductoSeleccionado.push(objetoEnviar);
            

        },
        procesarEliminarBebida(indiceEliminar){ 
            this.arregloProductoSeleccionado.splice(indiceEliminar,1); 
        },

    }
}



</script>
<style scoped>
#ComponentePadre{
    background-color: chartreuse;
}

</style>