<template>
    <div id="ProductoTragos">
        <h3>Productos</h3>
        <h4>Generacion de Contenido dinamicamente con v-for</h4>
        <div class="container">
            <div class="row">
                <div v-for="(datos, index) in informacionBebidas" :key="index" class="col-md-4">
                    <div class="card" style="width: 18rem;">
                        <img v-bind:src="datos.imgBebida" class="card-img-top" v-bind:alt="datos.tituloBebida">
                        <div class="card-body">
                            <h5 class="card-title">{{datos.tituloBebida}}</h5>
                            <p class="card-text">{{datos.descripcionBebida}}</p>
                            <a href="#" class="btn btn-primary" v-on:click.prevent="agregarBebida(index)">Agregar al carro</a>
                        </div>
                    </div>
                </div><br>          
            </div>
        </div>           
    </div>
</template>
<script>
export default{
    name:'ProductoTragos',
    props:{
        informacionBebidas:{
            type: Array,
            required: true,
        }
    },
    data: function(){
        return{

        }
    },
    methods:{
        agregarBebida: function(indice){
            console.log('indice de la bebida seleccionada: ', indice);
            this.$emit('bebidaSeleccionada', indice);
        }

    }
}

</script>    
